html {
  box-sizing: border-box; }

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  line-height: 1;
  font-weight: 400;
  color: #000;
  position: relative;
  height: 100%;
  background: #fff;
  -webkit-tap-highlight-color: transparent; }

div,
section,
footer,
.btn {
  box-sizing: border-box; }

a {
  color: #000;
  -webkit-transition: all 0.1s linear;
  -moz-transition: all 0.1s linear;
  -ms-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
  text-decoration: none; }

a:hover {
  text-decoration: none; }

a:focus,
input:focus {
  outline: none; }

p {
  color: #000;
  font-size: 20px;
  font-weight: 400;
  line-height: 29px;
  margin: 0 0 10px 0; }

input {
  display: block;
  width: 100%;
  font-size: 30px;
  font-weight: 700;
  line-height: 29px;
  padding: 0 8px;
  margin-bottom: 20px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.1s linear;
  -moz-transition: all 0.1s linear;
  -ms-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear; }

input:hover,
input:focus {
  outline: none; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

ul li {
  position: relative;
  padding: 0 0 0 0; }

button {
  border: none;
  outline: none;
  cursor: pointer;
  -webkit-transition: all 0.1s linear;
  -moz-transition: all 0.1s linear;
  -ms-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear; }

.title {
  color: #000;
  font-size: 42px;
  font-weight: 700;
  line-height: 53px;
  margin-bottom: 20px; }

.desc {
  color: #000;
  font-size: 20px;
  font-weight: 400;
  line-height: 29px;
  margin-bottom: 15px; }

img {
  max-width: 100%;
  max-height: 100%;
  display: block; }

.clearfix:after {
  content: '';
  display: table;
  clear: both; }

.wrapper {
  position: relative; }

.container {
  position: relative;
  max-width: 1562px;
  width: 100%;
  margin: 0 auto;
  padding: 0 16px; }

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

/* I E   S T Y L E S */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .block {
    width: 100%; } }

#hamburger-icon {
  position: relative;
  display: none;
  width: 18px;
  height: 14px; }

#hamburger-icon .line {
  position: absolute;
  left: 0;
  display: block;
  background: #ff6543;
  width: 18px;
  height: 2px;
  transition: .4s;
  -webkit-transition: .4s;
  -moz-transition: .4s; }

.mobile-menu {
  display: none; }

.line.line-1 {
  top: 0; }

.line.line-3 {
  top: 100%; }

.line.line-2 {
  top: 50%; }

#hamburger-icon.active .line-1 {
  transform: translateY(7px) translateX(0) rotate(45deg);
  -webkit-transform: translateY(7px) translateX(0) rotate(45deg);
  -moz-transform: translateY(7px) translateX(0) rotate(45deg);
  -ms-transform: translateY(7px) translateX(0) rotate(45deg);
  -o-transform: translateY(7px) translateX(0) rotate(45deg); }

#hamburger-icon.active .line-2 {
  opacity: 0; }

#hamburger-icon.active .line-3 {
  transform: translateY(-7px) translateX(0) rotate(-45deg);
  -webkit-transform: translateY(-7px) translateX(0) rotate(-45deg);
  -moz-transform: translateY(-7px) translateX(0) rotate(-45deg);
  -ms-transform: translateY(-7px) translateX(0) rotate(-45deg);
  -o-transform: translateY(-7px) translateX(0) rotate(-45deg); }

.ov-hidden {
  overflow: hidden; }

@media screen and (min-width: 992px) {
  .btn-orange:hover {
    background: #ec4824; } }

header {
  padding: 23px 0 0 0; }
  header .container {
    display: flex;
    justify-content: space-between;
    align-items: center; }

.header-left,
.header-right {
  width: 50%; }

.header-left {
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.header-right a {
  display: block;
  width: 24px;
  height: 24px; }

.logo {
  display: block;
  width: 100px;
  height: 32px;
  margin-right: 49px; }

.menu {
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  .menu li {
    margin-right: 50px; }
    .menu li:last-child {
      margin-right: 0; }
  .menu a {
    font-weight: 500;
    font-size: 14px;
    line-height: 100%;
    color: #000000; }

/* 
    M O B I L E
*/
@media screen and (max-width: 1200px) {
  html,
  body {
    min-width: 320px;
    width: 100%;
    overflow-x: hidden; }
  a:focus {
    outline: none; } }

@media screen and (max-width: 991px) {
  #hamburger-icon {
    display: block; }
  .mobile-menu {
    position: fixed;
    top: 68px;
    right: 0;
    width: 100vw;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    height: calc(100vh - 68px);
    z-index: 30;
    overflow: auto;
    background: rgba(21, 32, 39, 0.95);
    padding: 35px 15px 25px;
    box-sizing: border-box;
    transform: translate(100vw, 0);
    -webkit-transform: translate(100vw, 0);
    -moz-transform: translate(100vw, 0);
    -ms-transform: translate(100vw, 0);
    -o-transform: translate(100vw, 0);
    transition: .3s ease-in-out;
    -webkit-transition: .3s ease-in-out;
    -moz-transition: .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: .3s ease-in-out; }
  .mobile-menu.active {
    transform: translate(0, 0);
    -webkit-transform: translate(0, 0);
    -moz-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    -o-transform: translate(0, 0); }
  .mobile-menu .menu {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px; }
  .mobile-menu .menu li {
    display: block;
    margin: 0 0 20px 0; }
  .mobile-menu .menu a {
    font-size: 24px; } }
